import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Router } from '@angular/router';
import { User } from './User';
import { routerNgProbeToken } from '@angular/router/src/router_module';

const httpPostOptions = {
  headers: new HttpHeaders({ 
    'Content-Type': 'application/json', 
    'Access-Control-Allow-Origin': '*',
    'Access-Control-Allow-Headers': 'Origin, Content-Type, X-Auth-Token'
  })
};
const httpGetOptions = {
  headers: new HttpHeaders({ 
    'Access-Control-Allow-Origin': '*',
    'Access-Control-Allow-Headers': 'Origin, Content-Type, X-Auth-Token'
  })
};

@Injectable({
  providedIn: 'root'
})
export class UserService {
  url:string = "http://localhost:8080/";
  users:User[];
  loggedInUser:User;

  constructor(
    private http: HttpClient,
    private router: Router
  ) { }

  authenticate(user: User): Observable<User> {
    console.log('authenticating... ' + user.username + ', ' + user.password);
    return this.http.post<User>(this.url.concat('authenticate'),
      JSON.stringify(user), httpPostOptions)
  }

  login() : void {
    this.router.navigate([this.url.concat('/login')]);
  }

  addNewUser(user:User) : Observable<User> {
    console.log('adding new user... '+user.username+', '+user.password);
    return this.http.post<User>(this.url.concat('register'), 
      JSON.stringify(user), httpPostOptions)
  }
  
  addLoggedInUser(user: User) {
    this.users.push(user);
  }

}
